<template>
  <div class="login">
    <div class="login__wrapper">
      <div class="login__icon-people" />
      <div class="login__container">
        <el-tabs
          v-model="activeName"
          class="login__el-tabs"
          stretch
          @tab-click="handleClick"
        >
          <el-tab-pane v-if="showLoginUserId" label="成员ID登录" name="third">
            <div
              id="login_user_id_container"
              class="login__el-tabs__item-container"
            >
              <el-form
                ref="form"
                class="user-id-login-form"
                :model="userInfo"
                label-width="55px"
              >
                <el-form-item label="成员ID">
                  <el-input
                    v-model="userInfo.userId"
                    placeholder="请输入成员ID"
                  ></el-input>
                </el-form-item>
                <el-form-item>
                  <el-button
                    class="align-center"
                    type="primary"
                    @click="onSubmitLogin"
                    id="login-user-id-btn"
                  >
                    确认登录
                  </el-button>
                </el-form-item>
              </el-form>
            </div>
          </el-tab-pane>
          <el-tab-pane label="扫码登录" name="first">
            <div
              id="login_qr_container"
              class="login__el-tabs__item-container"
            />
          </el-tab-pane>
          <el-tab-pane label="账号登录" name="second">
            <div
              id="login_password_container"
              class="login__el-tabs__item-container"
            />
          </el-tab-pane>
        </el-tabs>
      </div>
    </div>
  </div>
</template>
<script>
import { on, off } from '@/utils/tools'
import DDLogin from '@/utils/ddLogin'
import { getLoginInfo } from '@/api/user'
import { getToken } from '@/utils/auth'

function getRealToken() {
  const token = getToken() || ''
  return token.replace('Bearer ', '')
}
export default {
  name: 'DingDing',
  data() {
    return {
      activeName: 'first',
      showLoginUserId: false,
      userInfo: {
        userId: ''
      },
      hanndleMessage: () => {},
      url: ''
    }
  },
  created() {
    getLoginInfo().then(res => {
      const { appid, redirect_uri } = res.data
      this.APPID = appid
      this.url = encodeURIComponent(
        `${redirect_uri}?redirect=${this.$route.query.redirect}`
      )
      this.initLoginQrContainer()
    })
  },
  mounted() {
    // 判断当前登录环境是线上还是测试环境
    let windowHost = window.location.host
    if (windowHost !== 'oa.baobaohehu.com') {
      this.showLoginUserId = true
      this.activeName = 'third'
    }
    const that = this
    this.hanndleMessage = event => {
      var origin = event.origin
      // window messageEvent
      // 判断是否来自ddLogin扫码事件。
      if (origin === 'https://login.dingtalk.com') {
        const loginTmpCode = event.data
        const href = `https://oapi.dingtalk.com/connect/oauth2/sns_authorize?appid=${that.APPID}&response_type=code&scope=snsapi_login&state=STATE&redirect_uri=${that.url}&loginTmpCode=${loginTmpCode}`
        const iframe = document.querySelector('#login_qr_container iframe')
        iframe.src = href
        // window.location.href = href
      }
      if (event.data.code) {
        that.handlerCodeLogin(event.data)
        // window.location.href = origin + '/?code=' + event.data.code + '&redirect=' + event.data.redirect || ''
      }
    }
    on(window, 'message', this.hanndleMessage)
  },
  beforeDestroy() {
    off(window, 'message', this.hanndleMessage)
  },
  methods: {
    thirdpartyLogin() {
      console.log('thirdpartyLogin')
      if (this.$route.query && this.$route.query.model_url) {
        console.log('thirdpartyLogin - 1')
        const modelUrl = this.$route.query.model_url
        const modelRedirect = this.$route.query.model_redirect || '/'
        window.location.replace(
          `${modelUrl}?_token=${getRealToken()}&redirect_url=${modelRedirect}`
        )
        return true
      }
      console.log('thirdpartyLogin - 2')
      return false
    },
    onSubmitLogin() {
      if (!this.userInfo.userId) {
        this.$message({
          type: 'warning',
          message: '用户ID不能为空，请输入用户ID'
        })
        return
      }
      this.$store
        .dispatch('user/setForeverToken', this.userInfo.userId)
        .then(res => {
          if (res.token) {
            if (!this.thirdpartyLogin()) {
              this.$router.push({ path: '/' })
            }
          }
        })
    },
    handleClick(tab, event) {
      if (
        document.querySelector('#login_password_container') &&
        !document.querySelector('#login_password_container iframe')
      ) {
        this.initLoginPasswordContainer()
      }
    },
    initLoginPasswordContainer() {
      const goto = encodeURIComponent(
        `https://oapi.dingtalk.com/connect/oauth2/sns_authorize?appid=${this.APPID}&response_type=code&scope=snsapi_login&state=password&redirect_uri=${this.url}`
      )
      // eslint-disable-next-line no-undef
      DDLogin({
        id: 'login_password_container',
        goto,
        style: 'border:none;background-color:#FFFFFF;',
        width: '400',
        height: '400'
      })
    },
    initLoginQrContainer() {
      const goto = encodeURIComponent(
        `https://oapi.dingtalk.com/connect/oauth2/sns_authorize?appid=${this.APPID}&response_type=code&scope=snsapi_login&state=qrcode&redirect_uri=${this.url}`
      )
      // eslint-disable-next-line no-undef
      DDLogin({
        id: 'login_qr_container',
        goto,
        style: 'border:none;background-color:#FFFFFF;',
        width: '400',
        height: '400',
        type: 'qr'
      })
    },
    handlerCodeLogin(query) {
      const that = this
      return new Promise((resolve, reject) => {
        if (query.code) {
          this.$store
            .dispatch('user/login', {
              code: query.code
            })
            .then(() => {
              if (!this.thirdpartyLogin()) {
                that.$router.push(query.redirect || '/')
                resolve()
              }
            })
            .catch(() => {
              // that.$router.push('/')
              that.initLoginQrContainer()
              that.initLoginPasswordContainer()
            })
        }
      })
    }
  }
}
</script>
<style lang="scss">
.login__container {
  .el-tabs {
    &__header {
      margin-bottom: 0;
    }
    &__nav {
      width: 100%;
    }
    &__item {
      padding: 0;
      height: 64px;
      line-height: 64px;
      font-size: 18px;
      text-align: center;
    }
    #login_qr_container,
    #login_user_id_container {
      width: 400px;
      height: 400px;
    }
    #login_user_id_container {
      .user-id-login-form {
        margin: 0 auto;
        padding-top: 50px;
        width: 94%;
        #login-user-id-btn {
          display: inline-block;
          margin-left: -51px;
          width: 117%;
          font-size: 18px;
          text-align: center;
          color: #fff;
          background: #008be6;
        }
      }
    }
  }
}
</style>
<style lang="scss" scoped>
@media (min-width: 1280px) {
  .login__wrapper {
    height: 637px;
  }
}
@media (min-width: 1480px) {
  .login__wrapper {
    height: 700px;
  }
}
@media (min-width: 1640px) {
  .login__wrapper {
    height: 760px;
  }
}
.login {
  display: flex;
  align-items: center;
  width: 100vw;
  height: 100vh;
  background-color: #0085ff;
  &__wrapper {
    display: flex;
    align-items: center;
    overflow: hidden;
    width: 100%;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    background-image: url('../../assets/loginBG.png');
    box-sizing: content-box;
  }
  &__container {
    width: 400px;
    background-color: #fff;
    border: 10px solid #dbeeff;
    box-sizing: content-box;
    user-select: none;
  }
  &__icon-people {
    margin: 0 20px;
    width: 58%;
    height: 500px;
  }
  &__el-tabs {
    &__item-container {
      margin-bottom: -3px;
    }
  }
}
</style>
